@import url(https://fonts.googleapis.com/css?family=Lato|Roboto|Poppins);
:root {
  --sideBar-color: #252C46;
  --sideBarText-color: #BEBEBE;
  --sideBarTextHover-color: white;
  --sideBarHoverAccent-color: #39446c;

  --contentBg-color: #1B2137;
  --box-color: #1e2439;
  --inputFont-color: #eff6f7;
  --inputBg-color: #1B2137;

  --tableRowHov-color: #2c3453;
}

[tabindex] {
  outline: none;
}

body {
  margin: 0;
  background-color: #252C46;
  background-color: var(--sideBar-color);
  padding: 0;
  height: 100vh;
  width: 100vw;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100%;
  width: 100%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.mainWrap {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 5% 95%;
    grid-template-columns: 15% 85%;
  }

  .navLogoContainer {
    background-color: var(--sideBar-color);
    display: grid;
  }

  .navLogo {
    margin: auto
  }

  .navLogo h1 {
    color: white;
    margin: 0;
    font-size: 1em;
  }


  
.navBar {
    background-color: var(--sideBar-color);
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    grid-row-start: 2;
    grid-row-end: span 2;
}

.navBarItem {
    color: var(--sideBarText-color);
    padding: 12px 10px;
    cursor: pointer;
    font-family: 'Poppins';
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    padding-bottom: 15px;
    border-right: 5px solid var(--sideBar-color);
}

.navActive {
    border-right: 5px solid var(--sideBarHoverAccent-color);
}

.navBarItem:hover {
    color: var(--sideBarTextHover-color);
}

.navbarIcon {
    margin-right: 1.25em;
}

.menu-icon {
    margin-right: 16px;
    width: 16px;
    line-height: 1;
    font-size: 14px;
    color: #979797;
}

.menu-icon svg {
    font-size: 14px;
    width: 20px !important;
}

.menu-title {
    color: inherit;
    display: inline-block;
    font-size: 14px;
    line-height: 1;
    vertical-align: middle;

}


.horNavBar {
    grid-column-start: 2;
    grid-column-end: span 2;
    background-color: var(--sideBar-color);
}
.main {
    background-color: red;
}
.homeContainer {
    background-color: var(--contentBg-color);
}
.dbTestContainer {
  background-color: var(--contentBg-color);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px;
  padding: 20px;
}

.dbTable {
  background-color: white;
}

.reactTableContainer {
}
.ReactTable {
  border: 0px solid white !important;
  border-radius: 10px;
  background-color: var(--box-color);
  width: 100%;
  height: 500px;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
  margin: auto;
}

.rt-thead {
  background-color: var(--sideBar-color);
  height: 50px;
}

.rt-th {
  color: white;
  font-family: "Poppins", sans-serif;
  text-align: left;
  opacity: 0.7;
  border-top: 2px solid transparent;
  border-bottom: 2px solid transparent;
  border-right: 0px solid !important;
  -webkit-transition: all 0.3s !important;
  transition: all 0.3s !important;
  padding: 10px !important;
}

.rt-th:nth-child(3),
.rt-td:nth-child(3) {
  text-align: right;
}

.ReactTable .rt-thead .rt-th.-sort-asc,
.ReactTable .rt-thead .rt-td.-sort-asc {
  border-top: 2px solid var(--sideBarHoverAccent-color);
  box-shadow: none !important;
}

.ReactTable .rt-thead .rt-th.-sort-desc,
.ReactTable .rt-thead .rt-td.-sort-desc {
  border-bottom: 2px solid var(--sideBarHoverAccent-color);
  box-shadow: none !important;
}

.rt-tbody {
  background-color: var(--box-color);
  border-radius: 0px 0px 10px 10px;
}

.rt-td {
  font-family: "Poppins";
}

.rt-tr-group {
  border-bottom: 0px solid white !important;
  padding: 5px;
  margin-top: 10px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  cursor: pointer;
  border-radius: 5px;
}

.rt-tr-group:hover {
  background-color: var(--tableRowHov-color);
}

.rt-td {
  border-right: 0px solid white !important;
  color: white;
}


.addStudentsContainer {
    width: 100%;
    
}
.studentsContainer {
    background-color: var(--contentBg-color);
    display: grid;
    grid-template-columns: 30% 70%;
}

.studentsForm {
    display: grid;
    width: 65%;
    margin: auto;
    border-radius: 10px;
    background-color: var(--box-color);
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
}

.studentsForm input {
    width: 100%;
    padding: 12px 20px;
    font-size: 1em;
    margin: 8px 0;
    box-sizing: border-box;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    border-radius: 10px;
    border: none;
    color: var(--inputFont-color);
    background-color: var(--inputBg-color);
    outline: none;
    -webkit-appearance: none;
    -webkit-transition: all 0.3s;
    font-family: 'Roboto';
    font-weight: 500;

}

.studentsForm input::-webkit-input-placeholder {
    color: var(--inputFont-color);
    opacity: 0.6;
}

.studentsForm input::-ms-input-placeholder {
    color: var(--inputFont-color);
    opacity: 0.6;
}

.studentsForm input::placeholder {
    color: var(--inputFont-color);
    opacity: 0.6;
}

.studentsForm input[type="button"] {
    width: 30%;
    border-radius: 5px;
    padding: 6px 10px;
    font-family: 'Poppins';
    cursor: pointer;
    color: white;
    background-color: #019875;
    float: right;
}

.studentsForm input[type="button"]:hover {
    -webkit-filter: brightness(0.85);
            filter: brightness(0.85)
}

.studentsForm .boxHeading {
    background-color: var(--sideBar-color);
    height: 50px;
}

.studentsForm .boxHeading h1 {
    font-family: 'Poppins', sans-serif;
    font-size: 1em;
    font-weight: 500;
    letter-spacing: 0.6px;
    opacity: 0.7;
    color: white;
    text-align: center;
}

.studentsFormWrap:before {
    content: "-";
    display: block;
    line-height: 1em;
    color: transparent;
}

.studentsTableContainer {
    width: 80%;
    margin: auto;
    margin-top: 25px;
    padding: 10px;
    border-radius: 10px;
    background-color: var(--box-color);
    max-height: 400px;
    overflow: scroll;
    
}


.currStudents--tableHeading {
    background-color: var(--sideBar-color);
}

.currStudents--tableHeading th:first-child {
    border-radius: 10px 0px 0px 10px
}

.currStudents--tableHeading th:last-child {
    border-radius: 0px 10px 10px 0px
}

.currStudents--tableHeading th {
    font-family: 'Poppins', sans-serif;
    font-size: 1em;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.6px;
    opacity: 0.7;
}

.currStudents--tableHeading th:nth-child(3) {
    text-align: right;
}


.currStudents--tableHeading td {
    font-family: 'Poppins', sans-serif;
    font-size: 1em;
    font-weight: 500;
}

.currStudents--head {
    width: 100%;
}

tbody:before {
    content: "-";
    display: block;
    line-height: 1em;
    color: transparent;
}

tbody:after {
    content: "-";
    display: block;
    line-height: 1em;
    color: transparent;
}



.currStudents--body tr:hover {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    background-color: #2c3453;
}

.currStudents--body tr:last-child {
    border-radius: 0px 10px 10px 0px
}

.currStudents--body td:first-child {
    border-radius: 10px 0px 0px 10px
}

.currStudents--body td:last-child {
    border-radius: 0px 10px 10px 0px
}

.currStudents--body td:nth-child(3),
.currStudents--body td:nth-child(4) {
    text-align: right;
}

.currStudents--hiddenText {
    opacity: 0;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    cursor: pointer;
}

.currStudents--body tr:hover .currStudents--hiddenText{
    opacity: 1;
}




table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    max-width: 100%;
    color: white;
}


th,
td {
    text-align: left;
    padding: 12px 10px;
}

caption {
    padding: 1rem 0;
    caption-side: bottom;
    color: #ababab;
}

.striped-table tbody tr:nth-child(odd) {
    background-color: #f8f8f8;
}

.contain-table {
    overflow-x: auto;
}

@media (min-width: 600px) {
    .contain-table {
        width: 100%;
    }
}
